import React, { Component } from "react"

class WebsiteCard extends Component {
  constructor(props) {
    super(props)
    this.handleClick = this.handleClick.bind(this)
  }

  handleClick(event) {
    event.preventDefault()
    event.stopPropagation()
    this.props.onWebsiteChange(this.props.website)
  }

  render() {
    var className =
      this.props.selectedWebsite === this.props.website
        ? "selected card"
        : "card"
    return (
      <div className={className} onClick={(e) => this.handleClick(e)}>
        {this.props.website}
      </div>
    )
  }
}

export default WebsiteCard
